body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  /* overflow-x: hidden; */
}

body{
    --textFontSize: 12px;
    --headTextFontSize: 16px
}
.graph{
    /* background-color: black; */
    width: 100%;
    height: auto;
    display: block;
    border: 1px solid;
    border-color: gray;
    border-radius: 5px;
    margin-bottom: 5%;
    padding: 1%;
    overflow-y: hidden;
    overflow-x: hidden;
}
.graph .button_container{
    display: flex;
    width: 100%;
    height: 40px;
    margin-bottom: 3%;

}
.graph .select_box{
    display: inline-block;
    width: 100%;
    margin-left: 10%;
    margin-right:10%;
    margin-top: 0.5%;
    padding: 1%;
    background-color: #ffd7e6;
    border: 1px solid palevioletred;
    border-radius: 10px;
    outline: none;  
    line-height: 50%;
    overflow-y: hidden;
    position: relative;

}
.select_box label{
    position: absolute;
    text-align: left;
    margin-left: 0%;
    left: 10px;
    top: 12px;
    bottom: 10px;
}
.select_box select{
    position: absolute;
    right: 10px;
    margin-right: 0%;
    margin-left: 5%;
    border-radius: 25px;
}

.grapher{
    width: 90%;
    margin-top: 2%;
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 2%;
    display:grid;
    justify-content: center;
    text-align: center;
    
}

.GraphicsPage{
    text-align: center;
    justify-content: center;
    overflow-x: hidden;
    margin: 0px;
}
.page_container{
    display: flex;
}
.graph_content{
    /* flex: 50%; */
    width: 74%;
    position: relative;
    left: 0px;
}
.graph_settings{
    /* flex:30%; */
    width: 26%;
    /* height: 100%; */
    border-left: 1px solid#000;
    margin: 0.5%;

}
.GraphicsPage .header{
    margin: 5%;
    margin-top: 2%;
    margin-bottom: 2%;
}
.GraphicsPage h2{
    text-align: center;
    font-size: var(--headTextFontSize);
}
.GraphicsPage p{
    text-align: justify;
    font-size: var(--textFontSize);
    margin: 5%;
    margin-top: 2%;
    margin-bottom: 2%;
}

.custom-tooltip{
    background-color: #fff;
    width: 230px; /*@TODO ADD MORE VALUE TOO SMALL */
    border: 1px solid #000;
    margin: 0px;
    text-align: left;
}
.navbar{
    background-color:#cd2464;
    height: 10%;
    margin-top: 0px;
    font-size: x-large;
    text-align: left;
    width: 100%;
    color: aliceblue;
    
}
.navbar ul{
    display: flex;
    width: 60%;
}
.navbar ul li{
    list-style: none;
    padding: 2%;
    display: flex;
}
.navbar ul li a{
    text-decoration: none;
    color: aliceblue;
    padding-top: 12%;
    padding-bottom: 12%;
    vertical-align:middle;
    display: flex;
}
.navbar svg{
    margin: 3%;
    width: 100%;
}
.logo{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.right{
    position: absolute;
    right: 0%;
    justify-content: center;
}
.sync_info{
    position: absolute;
    display: inline-block;
    width: 150px;
    height: 30px;
    right: 0px;
    
}
.sync_info > label{
    position: absolute;
    /* top: 0;
    left: 0;
    right: 0;
    bottom: 0; */
    padding: 4%;
    padding-left: 15%;
    padding-right: 15%;
    background-color: gray;
    border-radius: 100px;
    text-align: center;
    justify-content: center;
    font-size: large;
    border: solid #008008 1px;
    cursor: pointer;
    
}

.settings_page{
    text-align: center;
    right: 0px;
    position:fixed;
    width: inherit;
}

.title{
    padding: 0.5%;
    margin: 1%
}
.setting_box{
    display: block;
    justify-content: center;
    width: 100%;
    /* position: fixed; */
    margin: 1%;
    text-align:center;
    padding: 3%;

}
.metric_setting_box{
    display: block;
    justify-content: center;
    width: 100%;
    /* position: fixed; */
    /* margin-top:25%; */
    text-align:center;
    /* overflow-y: scroll; */
}
.metric_setting_box > .setting{
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
}
.setting{
    width: 80%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 0%;
    margin-bottom: 5%;
    padding: 4%;
    /* background-color: brown; */
    /* border: 1px solid gray; */
    background-color: #ffd7e6;
    border: 1px solid palevioletred;
    border-radius: 100px;
    font-size: large;
    justify-content: center;
    text-align: left;
    position: relative
}
.setting select{
    padding: 1%;
    margin: 4%;
    margin-right: 5%;
    width: auto;
    right: 0px;
    top: 0px;
    position: absolute;
    border-radius: 25px;
}
.setting input{
    padding: 2%;
    margin: 3%;
    width: 50px;
    right: 0px;
    top: 0px;
    position: absolute;
    border-radius: 25px;
    border: gray 1px solid;
}
.settings_toggle{
    /* position: fixed; */
    right: 20%;
    top:50%;
    background-color: #ffd7e6;
    border: 1px solid palevioletred;
}
.hide{
    display: none;
}
.min{
    width: 3% !important;
}
.full{
    width: 97% !important;
}
.right{
    right: 1%;
}
