}
.wiki_page {
    text-align: center;
    list-style-type: circle;
}
.wiki_page button{
    background-color: #ffd7e6;
    border: 1px solid palevioletred;
}
.wiki_page section{
    margin: 1%;
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 2%;
    text-align: left;
}
.wiki_page h3 {
    padding: 2%;
    padding-left: 0%;
    font-size: var(--h3fontSize);
}
.wiki_page h4{
    padding: 0.5%;
    font-size: 24px;
}

.wiki_page h2{
    margin-top: 2%;
    font-size: var(--h2fontSize);
}

.wiki_page p{
    padding: 0.5%;
