}

.container{
    display: flex;
}
.index{
    flex: 10% 1;
    border-right: 1px solid #000;
    justify-content: center;
    /* margin-top: 3%; */
    width: 10%;
    padding: 4%;
    font-size: large;
}
.index .static{
    left: 0px;
    top: 22%;
    position: fixed;
}
.index ul{
    text-align: left;
}
.index li{
    list-style-type: disc;
    padding: 3%;
    width: 100%;
}
.index li:hover{
    color: rgb(189, 1, 170);
    cursor: pointer;

}

.content{
    flex:90% 1;
    position: relative;
}
